












































import Vue from 'vue';
import { mapState, mapMutations } from 'vuex';
import PointTable from './pointTable.vue';
import PointFilter from './pointFilter.vue';
import DetailPoint from './detailPoint.vue';
import pointDetailOption from '../testMock/pointDetail';
import { ChildType } from '../types/interface';
export default Vue.extend({
    components: {
        PointTable,
        PointFilter,
        DetailPoint,
    },
    data() {
        return {
            statistics: ChildType.Statistics,
            engineValue: ChildType.EngineValue,

            checkPointList: [],
            // valueid
            pointListOptions: [{
                value: 'cedian1',
                label: '1',
                childValueTyle: 1,
                aggregate: '',
                period: '',
                periodAggregate: '',
            }, {
                value: 'cedian2',
                label: '2',
                childValueTyle: 1,
                aggregate: '',
                period: '',
                periodAggregate: '',
            },{
                value: 'cedian3',
                label: '3',
                childValueTyle: 1,
                aggregate: '',
                period: '',
                periodAggregate: '',
            },{
                value: 'cedian4',
                label: '4',
                childValueTyle: 1,
                aggregate: '',
                period: '',
                periodAggregate: '',
            },{
                value: 'cedian5',
                label: '5',
                childValueTyle: 1,
                aggregate: '',
                period: '',
                periodAggregate: '',
            },{
                value: 'cedian6',
                label: '6',
                childValueTyle: 1,
                aggregate: '',
                period: '',
                periodAggregate: '',
            }],
            pointCheckOptions: [],

            // 
            pointDetailOption: pointDetailOption,

            dialogVisible: false,
            currentPont: {
                childValueTyle: 1,
                aggregate: '',
                period: '',
                periodAggregate: '',
            },
            
            pointDetail: {
                //  - 
                childValueTyle: 1,
                //  - 
                period: '',
                //  - 
                aggregate: '',
                //  - 
                periodAggregate: '',
            },
            
            // 
            isEditPoint: false,
            // ...
            pointGroupPadding: false,
        };
    },
    computed: {
        ...mapState({
            partSelection: (state: any) => state.screen.editor.partSelection
        }),
        /**
         * part
         */
        selectGroup(): Model.Part[] {
            return this.partSelection.selectGroup
        },
        /**
         * part
         * part
         */
        curSelectPart(): Model.Part {
            const { length } = this.selectGroup
            return length === 1 && this.isReleasePart ? this.selectGroup[0] : {}
        },
        /**
         * part
         * part --  or 
         */
        isReleasePart(): boolean {
            return this.partSelection.releasePart;
        },
        partData(): any {
            const partData = this.curSelectPart.partData;
            return partData;
        },
        pointData(): any {
            const pointData =  (this.partData && this.partData.pointData) || {};
            return pointData;
        },
        pointGroup(): any[] {
            return (this.pointData && this.pointData.pointGroup) || [];
        }
    },
    watch: {
        curSelectPart(part: Model.Element) {
            if (!this.isReleasePart) {
                return
            }
            this.initPointCheckOptions();
        },
    },
    methods: {
        ...mapMutations([
            'set_partData',
        ]),
        /**
         * 
         */
        initPointDetail() {
            this.pointDetail.childValueTyle = 1;
            this.pointDetail.periodAggregate = '';
            this.pointDetail.aggregate = '';
            this.pointDetail.period = '';
        },
        /**
         * 
         * 
         * 
         */
        addCheckPoint() {
            // 
            this.modalMapToCurrentPoint();

            // 
            if (this.validateNullPoint()) {
                return;
            }

            //
            if (this.validateSamePoint()) {
                this.$message.error('');
                return;
            }

            // 
            this.dialogVisible = false;

            if (this.isEditPoint) {
                const index: number  = this.pointCheckOptions.findIndex( (item) => {
                    // id
                    return this.currentPont.id === item.id;
                });

                index !== -1 && this.pointCheckOptions.splice(index, 1 , this.currentPont);
                this.isEditPoint = false;
            } else {
                const copyCurPoint = _.cloneDeep(this.currentPont);
                const checkPointId = _.uniqueId('point_');
                Object.assign(copyCurPoint, {id: checkPointId});

                this.pointCheckOptions.push( copyCurPoint );
            }

            // 
            this.emitFetchPointData();
        },
        modalMapToCurrentPoint(): void {
            this.currentPont.childValueTyle = this.pointDetail.childValueTyle;
            this.currentPont.periodAggregate = this.pointDetail.periodAggregate;
            this.currentPont.aggregate = this.pointDetail.aggregate;
            this.currentPont.period = this.pointDetail.period;
        },
        /**
         * 
         */
        validateNullPoint(): boolean {
            console.log(this.currentPont);
            
            if (!this.currentPont.childValueTyle) {
                this.$message.error('');
                return true;
            }
            if (this.currentPont.childValueTyle === this.statistics) {
                if (!this.currentPont.periodAggregate) {
                    this.$message.error('');
                    return true;
                }
            }
            if (this.currentPont.childValueTyle === this.engineValue) {
                if (!this.currentPont.aggregate) {
                    this.$message.error('');
                    return true;
                }
                if (!this.currentPont.period) {
                    this.$message.error('');
                    return true;
                }
            }
            return false;
        },
        /**
         * 
         */
        validateSamePoint(): boolean {
             // 
            const isSamePoint = this.pointCheckOptions.some( (checkPoint: any) => {
                // valueid
                if (checkPoint.value !== this.currentPont.value) {
                    return false;
                }
                // 
                if (this.currentPont.childValueTyle && this.currentPont.childValueTyle === this.statistics) {
                    if (this.currentPont.periodAggregate === checkPoint.periodAggregate) {
                        return true;
                    }
                // ;
                } else if (this.currentPont.childValueTyle && this.currentPont.childValueTyle === this.engineValue) {
                    if (this.currentPont.aggregate === checkPoint.aggregate 
                    && this.currentPont.period === checkPoint.period) {
                        return true;
                    }
                }
            });
            return isSamePoint;
        },
        /**
         * 
         * 
         */
        getCheckPoint(point: any) {
            // 
            this.dialogVisible = true;
            this.initPointDetail();
            this.currentPont = _.cloneDeep(point);
        },
        deleCheckPoint(point: any) {
            const index: number  = this.pointCheckOptions.findIndex( (item) => {
                // id
                return point.id === item.id;
            });

            index !== -1 && this.pointCheckOptions.splice(index, 1);

            // 
            this.emitFetchPointData();
        },
        editCheckPoint(point: any) {
            this.isEditPoint = true;
            this.dialogVisible = true;
            this.currentPont = _.cloneDeep(point);
            
            this.pointDetail.childValueTyle = this.currentPont.childValueTyle;
            this.pointDetail.periodAggregate = this.currentPont.periodAggregate;
            this.pointDetail.aggregate = this.currentPont.aggregate;
            this.pointDetail.period = this.currentPont.period;
        },
        initPointCheckOptions() {
            if (this.pointGroup) {
                // 
                this.pointCheckOptions =  this.pointGroup.map( (item) => {
                    return {
                        value: item.pointId,
                        label: item.pointName,
                        aggregate: item.aggregate,
                        period: item.period,
                        childValueTyle: item.childValueTyle,
                        periodAggregate: item.periodAggregate,
                        id: _.uniqueId('point_'),
                    }
                });
            }
            this.resetPointListOptions();
        },
        // 
        resetPointListOptions() {
            for (const point of this.pointListOptions) {
                point.disable = false;
            }
        },
        // 
        handleDialogVisible(toggle: boolean): void {
            this.dialogVisible = toggle;
        },
        // 
        editPointDetail(key: string, value: string): void {
            (this.pointDetail as any)[key] = value;
        },
        /**
         * 
         */
        updatePointGroup() {
            const pointGroup =  this.pointCheckOptions.map( (item) => {
                return {
                    pointId: item.value,
                    pointName: item.label,

                    childValueTyle: item.childValueTyle,
                    periodAggregate: item.periodAggregate,
                    period: item.period,
                    aggregate: item.aggregate,
                }
            });
            const option = {
                pointGroup: pointGroup,
                mode: 'pointData',
            };
            this.set_partData(option);
        },
        /**
         * 
         * 
         */
        emitFetchPointData() {
            this.updatePointGroup();
            (this.$root as any).busRef.$emit('fetchPoint')
        },
    },
    created() {
        this.initPointCheckOptions();
    },
    mounted() {
        // console.log("point mounted");
    }
})
