/**
 * editor
 * originEditorRef editor
 * @param cb 
 */
export default (cb?: (toggle?: boolean) => void) => {
    return {
        data() {
            return {
                originEditorRef: {},
            };
        },
        methods: {
            /**
             * 
             */
            onListenerDrawer(isDestroyed?: boolean) {
                if (isDestroyed) {
                    (this.$root as any).busRef.$off('handleDrawer');
                    return;
                }
                (this.$root as any).busRef.$on('handleDrawer', (toggle: boolean) => {
                    // 
                    if (toggle) {
                        this.$nextTick( () => {
                            (this.originEditorRef as any).alignCursors();
                        });
                    }
                });
            },
            /**
             * 
             * @editor 
             */
            onMountedEditor(editor: any) {
                this.originEditorRef = editor;
            }
        },
        mounted() {
            this.onListenerDrawer();
        },
        beforeDestroy() {
            this.originEditorRef = null;
            this.onListenerDrawer(true);
        }
    }
}