


















































































import Vue from 'vue';
import AceEditor from './editor/ace.vue';
import { mapState, mapMutations } from 'vuex';
import { fetchPointData } from './utils/fetchPointData';
import { excuteScriptCode } from './utils/excuteScriptCode';
import { DataSource } from './types/interface';
// 
const popoverDelay = 50;
export default Vue.extend({
    data() {
        return {
            isDebug: false,
            filterCode: '',
            
            // 
            inputData: '',
            outputData: '',
            pointData: '',

            // editor
            originEditorRef: {},
            inputEditorRef: {},
            outputEditorRef: {},

            editorloading: false,

            // 
            pointReqData: '',

            // 
            codeHasSave: true,
        }
    },
    props: {
        drawerVisble: Boolean,
    },
    components: {
        AceEditor
    },
    computed: {
        ...mapState({
            partSelection: (state: any) => state.screen.editor.partSelection,
            // vuex
            dataSource: (state: any) => state.screen.editor.dataSource,
        }),
        /**
         * part
         */
        selectGroup(): Model.Part[] {
            return this.partSelection.selectGroup
        },
        /**
         * part
         * part
         */
        curSelectPart(): Model.Part {
            const { length } = this.selectGroup
            return length === 1 ? this.selectGroup[0] : {}
        },
        partData(): any {
            const partData = this.curSelectPart.partData;
            return partData;
        },
        popVisible(): boolean {
            return this.isDebug;
        },
        sourceType(): number {
            return this.partData && this.partData.sourceType || 0;
        },
        // 
        useScript: {
            get(): string {
                return (this.partData && this.partData.useScript) || false;
            },
            set(isUseScript: string): void {
                if (!isUseScript) {
                    this.isDebug = false;
                }
                setTimeout(() => {
                    const option = {
                        useScript: isUseScript,
                    }
                    this.set_partData(option);
                }, popoverDelay);
            }
        },
        sourceData() {
            return this.partData && this.partData.staticData && this.partData.staticData.sourceData;
        },
        // 
        displayDataPool(): string {
            return this.dataSource &&  this.dataSource.displayData || '[]';
        },
        drawerStatus() {
            return this.dataSource &&  this.dataSource.drawerStatus || false;
        },
        scriptData: {
            get(): string {
                return (this.partData && this.partData.scriptLog) || 'return data';
            },
            set(newScriptData: string): void {
                const option = {
                    scriptLog: newScriptData,
                }
                this.set_partData(option);
            }
        },
    },
    watch: {
        drawerVisble(newValue) {
            if (!newValue) {
                // 
                this.isDebug = false;
                // 
                this.outputData = '[]';
            } else {
                // 
                this.updataInputData();
            }
        },
        // 
        pointReqData() {
            // 
            this.updataInputData();

        },
        useScript(isUseScript: boolean) {
            // 
            if (isUseScript) {
                this.updateScriptData();
            }
        },
        /**
         * 
         * 
         * 
         */
        isDebug(newIsDebug: boolean) {
            (this.$root as any).busRef.$emit('changeEditorTest', newIsDebug)
        },
        /**
         * 
         */
        drawerStatus(): void {
            if(!this.drawerStatus) {
                return;
            }
            this.updateScriptData();
        }
    },
    methods: {
        ...mapMutations([
            'set_partData',
        ]),
        /**
         * 
         */
        updataInputData() {
            let initInputData = '[]';
            if (this.sourceType === DataSource.Static) {
                initInputData = this.sourceData;
            } else if (this.sourceType === DataSource.Point){
                initInputData = this.pointReqData;
            }
            this.inputData = initInputData;
        },
        useScriptChange() {
            // 
            setTimeout(() => {
                this.onEmitScriptChange();
            }, popoverDelay);
        },
        /**
         * 
         * 1. initInputData
         */
        excuteDebug() {
            // 
            const inputSourceData: any = _.isEmpty(this.curSelectPart) ? [] : JSON.parse(this.inputData);
            // 
            const outpuSourcetData = excuteScriptCode(inputSourceData, this.scriptData);
            // 
            this.outputData = JSON.stringify(outpuSourcetData, null, 2);
        },
        /**
         * 
         * 
         */
        saveScriptLog() {
            // 
            this.isDebug = false;

            // 
            this.onEmitScriptChange();

            this.$message.success('');
            // 
            this.codeHasSave = true;
        },
        /**
         * 
         */
        updateFilterCode(filterCode: any) {
            // 
            if (filterCode === this.scriptData) {
                return;
            }
            this.codeHasSave = false;
            this.scriptData = filterCode;
        },
        /**
         * 
         */
        onEmitScriptChange() {
            (this.$root as any).busRef.$emit('scriptChange');
        },
        /**
         * 
         * @editor 
         */
        onMountedEditor(editor: any) {
            this.originEditorRef = editor;
        },
        mountedInputEditorRef(editor: any) {
            this.inputEditorRef = editor;
        },
        mountedOutputEditorRef(editor: any) {
            this.outputEditorRef = editor;
        },
        /**
         * 
         * 
         * 1. 
         * 2. 
         */
        onListenerFetchPoint(isDestroyed?: boolean): void {
            if (isDestroyed) {
                (this.$root as any).busRef.$off('fetchPoint');
                return;
            }
            (this.$root as any).busRef.$on('fetchPoint', () => {
                if (this.isDebug) {
                    this.fetchPointInputData();
                }
            });
        },
        /**
         * 
         * 
         * 1. 
         * 2. 
         */
        onListenerStaticData(isDestroyed?: boolean) {
            if (isDestroyed) {
                (this.$root as any).busRef.$off('fetchStatic');
                return;
            }
            (this.$root as any).busRef.$on('fetchStatic', (message) => {
                // 
                if (this.isDebug) {
                    this.fetchStaticData();
                }
            });
        },
        fetchPointInputData() {
            // 
            if (!this.isDebug) {
                this.onEmitScriptChange();
                return;
            }
            this.editorloading = true;
            fetchPointData().then(
                (success: any) => {
                    this.pointReqData = JSON.stringify(success, null, 2)
                    this.editorloading =false;
                    // this.onEmitScriptChange(this.pointReqData);
                },
                (error: any) => {
                    // 
                    this.editorloading =false;
                }
            );
        },
        fetchStaticData() {
            let displayData = '';
            if (this.sourceData && _.isString(this.sourceData)) {
                try {
                    displayData = JSON.parse(this.sourceData);
                } catch (error) {
                    displayData = '';
                }
            }
            this.pointReqData = JSON.stringify(displayData, null, 2)
        },
        debugChange() {
            if (this.isDebug && this.sourceType === DataSource.Point) {
                this.fetchPointInputData();
            } else if (this.isDebug && this.sourceType === DataSource.Static){
                this.fetchStaticData();
            }
            this.updateIOEditorData();
        },
        /**
         * 
         * fiexme 
         * 1.  2. 
         */
        updateIOEditorData() {
            this.$nextTick( () => {
                (this.inputEditorRef as any).alignCursors();
                (this.outputEditorRef as any).alignCursors();
            });
        },
        /**
         * 
         * 1.  2. 
         */
        updateScriptData() {
            this.$nextTick( () => {
                (this.originEditorRef as any).alignCursors();
            });
        },
    },
    mounted() {
        this.updataInputData();
        this.onListenerFetchPoint();
        this.onListenerStaticData();
    },
    beforeDestroy() {
        this.originEditorRef = null;
        this.onListenerFetchPoint(true);
        this.onListenerStaticData(true);
    }
})
