





















































import Vue from 'vue';
import { mapDataSource } from '../utils/mapDataSource';
import { mapState, mapMutations } from 'vuex';
export default Vue.extend({
    data() {
        return {
            pointTypeOptions: [{
                value: 'leijiliang',
                label: ''
            }, {
                value: 'zifuchuanliang',
                label: ''
            }],

            deviceTypeOptions: [{
                value: 'kongtiao',
                label: ''
            }, {
                value: 'wangguan',
                label: ''
            }],

            deviceOptions: [{
                value: 'leijiliang',
                label: '1'
            }, {
                value: 'zifuchuanliang',
                label: '2'
            }],
        }
    },
    computed: {
        ...mapState({
            partSelection: (state: any) => state.screen.editor.partSelection
        }),
        ...mapDataSource({
            pointType: {
                // 
                carrier: 'pointData',
                // 
                getOption: (newValue: any) => {
                    return {
                        pointType: newValue,
                        mode: 'pointData',
                    }
                },
            },
            pointDeviceType: {
                carrier: 'pointData',
                getOption: (newValue) => {
                    return {
                        pointDeviceType: newValue,
                        mode: 'pointData',
                    }
                },
            },
            pointDevice: {
                carrier: 'pointData',
                getOption: (newValue) => {
                    return {
                        pointDevice: newValue,
                        mode: 'pointData',
                    }
                },
            },
            pointName: {
                carrier: 'pointData',
                getOption: (newValue) => {
                    return {
                        pointName: newValue,
                        mode: 'pointData',
                    }
                },
            },
        }),
        /**
         * part
         */
        selectGroup(): Model.Part[] {
            return this.partSelection.selectGroup
        },
        /**
         * part
         * part
         */
        curSelectPart(): Model.Part {
            const { length } = this.selectGroup
            return length === 1 && this.isReleasePart ? this.selectGroup[0] : {}
        },
        /**
         * part
         * part --  or 
         */
        isReleasePart(): boolean {
            return this.partSelection.releasePart;
        },
        partData(): any {
            const partData = this.curSelectPart.partData;
            return partData;
        },
        pointData(): any {
            const pointData =  (this.partData && this.partData.pointData) || {};
            return pointData;
        },
    },
    methods: {
        ...mapMutations([
            'set_partData',
        ]),
    }
})
